import React from 'react';
import Campo from "./_shared/Campo";
import Combo from "./_shared/Combo";

export default class Passo2 extends React.Component {
    constructor(props) {
        super(props);
        this.state = props.state;

        this.handleInputChange = this.handleInputChange.bind(this);
        this.handlePercentualBasicoChange = this.handlePercentualBasicoChange.bind(this);
        this.handleRiscoInvalidezChange = this.handleRiscoInvalidezChange.bind(this);
        this.handleRiscoPensaoMorteChange = this.handleRiscoPensaoMorteChange.bind(this);
        this.verificarValorMaximo = this.verificarValorMaximo.bind(this);
        this.somaAdicional = this.somaAdicional.bind(this);
        this.renderTitulo = this.renderTitulo.bind(this);
    }

    handlePercentualBasicoChange(event) {
        var valor = parseFloat(event.target.value);

        // Multiplica o valor do percentual basico por 0.06
        this.setState({
            carregamentoContribuicaoBasica: (valor * 0.06).toFixed(2)
        });

        this.handleInputChange(event);
    }

    handleRiscoInvalidezChange(event) {
        var valor = parseFloat(event.target.value);
        
        this.setState({
            beneficioRiscoPensaoMorteMax: (1 - valor).toFixed(2),
            beneficioRiscoSoma: (parseFloat(valor) + parseFloat(this.state.beneficioRiscoPensaoMorte)).toFixed(2)
        }, () => {
            console.log(this.state)
        });

        this.handleInputChange(event);
    }

    handleRiscoPensaoMorteChange(event) {
        var valor = parseFloat(event.target.value);

        this.setState({
            beneficioRiscoInvalidezMax: (1 - valor).toFixed(2),
            beneficioRiscoSoma: (parseFloat(valor) + parseFloat(this.state.beneficioRiscoInvalidez)).toFixed(2)
        }, () => {
            console.log(this.state)
        });

        this.handleInputChange(event);
    }

    handleInputChange(event, callback) {
        const target = event.target;
        const value = (target.type === 'checkbox' ? target.checked : target.value).replace(',', '.');
        const name = target.name;

        console.log(value);
        console.log(name);

        this.setState({
            [name]: value
        }, () => {
            if(callback) callback();
            else console.log(this.state)
        });
    }
    
    renderPercentual(valor) {
        return valor.replace('.', ',');
    }

    verificarValorMaximo(campo, valorMaximo) {
        var valor = parseFloat(campo);

        return valor !== valorMaximo;
    }

    somaAdicional(event) {
        this.handleInputChange(event, () => {
            var soma = parseFloat(this.state.aposentadoriaAdicional) +
                   parseFloat(this.state.invalidezAdicional) +
                   parseFloat(this.state.pensaoMorteAdicional) +
                   parseFloat(this.state.sobrevivenciaAdicional);
            
            soma = soma * 0.06;

            this.setState({
                carregamentoContribuicaoAdicional: soma.toFixed(2)
            }, () => {
                console.log(this.state)
            });
        });
    }

    renderTitulo() {
        return (<h2 align="center">Ótimo {this.props.nome}! Agora vamos definir a sua contribuição para o Plano PREVES SE!</h2>);
    }

    componentDidMount() {
        window.scrollTo(0, 0);
    }

    render() {
        return (
            <div>
                {this.renderTitulo()}
                <div className="form-group row">
                    <div className="col-2 offset-5 text-center">
                        <h5 className="text-secondary">Participante</h5>
                    </div>
                    <div className="col-2 text-center">
                        <h5 className="text-secondary">Patrocinador</h5>
                    </div>
                </div>

                <Campo label="Percentual Básico" usaBotaoAjuda>
                    <div className="input-group col-2 text-center">
                        <Combo id="percentualBasico" min={3.00} max={8.5} incremento={0.5} decimais 
                            value={this.state.percentualBasico} onChange={this.handlePercentualBasicoChange} />
                        <div className="input-group-append">
                            <span className="input-group-text">%</span>
                        </div>
                    </div>
                    
                    <div className="form-control-plaintext col-2 text-center">
                        {this.renderPercentual(this.state.percentualBasico)}%
                    </div>
                </Campo>

                <Campo label="Benefício de Risco - Invalidez" usaBotaoAjuda>
                    <div className="input-group col-2 text-center">
                        <Combo id="beneficioRiscoInvalidez" min={0} max={this.state.beneficioRiscoInvalidezMax} incremento={0.10} decimais 
                            value={this.state.beneficioRiscoInvalidez} onChange={this.handleRiscoInvalidezChange} />
                        <div className="input-group-append">
                            <span className="input-group-text">%</span>
                        </div>
                    </div>
                    
                    <div className="form-control-plaintext col-2 text-center">
                        {this.renderPercentual(this.state.beneficioRiscoInvalidez)}%
                    </div>
                </Campo>

                <Campo label="Benefício de Risco - Pensão por Morte" usaBotaoAjuda>
                    <div className="input-group col-2 text-center">
                        <Combo id="beneficioRiscoPensaoMorte" min={0} max={this.state.beneficioRiscoPensaoMorteMax} incremento={0.10} decimais 
                            value={this.state.beneficioRiscoPensaoMorte} onChange={this.handleRiscoPensaoMorteChange} />
                        <div className="input-group-append">
                            <span className="input-group-text">%</span>
                        </div>
                    </div>
                    
                    <div className="form-control-plaintext col-2 text-center">
                        {this.renderPercentual(this.state.beneficioRiscoPensaoMorte)}%
                    </div>
                </Campo>

                <Campo label="Sobrevivência" usaBotaoAjuda>
                    <div className="input-group col-2 text-center">
                        <Combo id="sobrevivencia" min={0} max={1} incremento={0.1} decimais 
                            value={this.state.sobrevivencia} onChange={this.handleInputChange} />
                        <div className="input-group-append">
                            <span className="input-group-text">%</span>
                        </div>
                    </div>
                    
                    <div className="form-control-plaintext col-2 text-center">
                        {this.renderPercentual(this.state.sobrevivencia)}%
                    </div>
                </Campo>

                <Campo label="Carregamento Contribuição Básica" usaBotaoAjuda>
                    <div className="form-control-plaintext col-2 text-center">
                        {this.renderPercentual(this.state.carregamentoContribuicaoBasica)}%
                    </div>
                    
                    <div className="form-control-plaintext col-2 text-center">
                        {this.renderPercentual(this.state.carregamentoContribuicaoBasica)}%
                    </div>
                </Campo>
                <br/>
                <br/>

                <h3 className="text-center">
                    Que tal contribuir adicionalmente para melhorar sua aposentadoria?
                </h3>
                <br/>

                <div className="row">
                    <div className="col offset-5">
                        <h5 className="text-secondary">Contribuição adicional</h5>
                    </div>
                </div>
                
                <Campo label="Aposentadoria" usaBotaoAjuda>
                    <div className="col-2">
                        <div className="input-group">
                            <input id="aposentadoria" name="aposentadoriaAdicional" className="form-control" placeholder="0,00" disabled={this.verificarValorMaximo(this.state.percentualBasico, 8.5)} 
                                onChange={this.somaAdicional} value={this.state.aposentadoriaAdicional} />
                            <div className="input-group-append">
                                <span className="input-group-text">%</span>
                            </div>
                        </div>
                    </div>
                </Campo>
                
                <Campo label="Invalidez" usaBotaoAjuda>
                    <div className="col-2">
                        <div className="input-group">
                            <input name="invalidezAdicional" className="form-control" placeholder="0,00" disabled={this.verificarValorMaximo(this.state.beneficioRiscoSoma, 1)} 
                                onChange={this.somaAdicional} value={this.state.invalidezAdicional} />
                            <div className="input-group-append">
                                <span className="input-group-text">%</span>
                            </div>
                        </div>
                    </div>
                </Campo>
                
                <Campo label="Pensão por Morte" usaBotaoAjuda>
                    <div className="col-2">
                        <div className="input-group">
                            <input name="pensaoMorteAdicional" className="form-control" placeholder="0,00" disabled={this.verificarValorMaximo(this.state.beneficioRiscoSoma, 1)} 
                                onChange={this.somaAdicional} value={this.state.pensaoMorteAdicional} />
                            <div className="input-group-append">
                                <span className="input-group-text">%</span>
                            </div>
                        </div>
                    </div>
                </Campo>
                
                <Campo label="Sobrevivência" usaBotaoAjuda>
                    <div className="col-2">
                        <div className="input-group">
                            <input name="sobrevivenciaAdicional" className="form-control" placeholder="0,00" disabled={this.verificarValorMaximo(this.state.sobrevivencia, 1)} 
                                onChange={this.somaAdicional} value={this.state.sobrevivenciaAdicional}/>
                            <div className="input-group-append">
                                <span className="input-group-text">%</span>
                            </div>
                        </div>
                    </div>
                </Campo>
                
                <Campo label="Carregamento contribuição Adicional" usaBotaoAjuda>
                    <div className="form-control-plaintext col-2 text-center">
                        {this.renderPercentual(this.state.carregamentoContribuicaoAdicional)}%
                    </div>
                </Campo>
                <br/>

                <div className="row justify-content-center">
                    <button className="btn btn-lg btn-dark mr-3" onClick={() => this.props.setPassoAtivo(1)}>Voltar</button>
                    <button className="btn btn-lg btn-primary" onClick={() => this.props.setPassoAtivo(3)}>Continuar</button>
                </div>
            </div>
        );
    }
}



