import React from "react";
import Campo from "./_shared/Campo";
import Combo from "./_shared/Combo";
import calculaIdade from "./_shared/Idade";
import DataInvalida from "./_shared/Data"

const textos = require("./Textos");

export default class Passo1 extends React.Component {
    constructor(props) {
        super(props);
        this.state = props.state;

        this.toggleTermos = this.toggleTermos.bind(this);
        this.onBlurData = this.onBlurData.bind(this);
        this.validaExtremosComboAposentadoria = this.validaExtremosComboAposentadoria.bind(this);
        this.validaPasso = this.validaPasso.bind(this);
        this.validarCampoVazio = this.validarCampoVazio.bind(this);
    }

    toggleTermos() {
        this.setState({ termosAceitos: !this.state.termosAceitos });
    }

    validarCampoVazio(valor, campoErro) {
        var campoInvalido = valor === "";
        this.setState({
            [campoErro]: campoInvalido
        });
        return campoInvalido;
    }
    
    // Método que checa se os campos não estão vazios para prosseguir para o próximo passo.
    validaPasso() {
        var nome = document.getElementById("nome").value;
        var data = document.getElementById("dataNascimento").value;
        var dataObjeto = this.converteData(data);

        var remuneracaoInicial = document.getElementById("remuneracaoInicial").value;
        var remuneracaoFinal = document.getElementById("remuneracaoFinal").value;
        
        // Verificação de campos obrigatórios.
        var nomeInvalido = this.validarCampoVazio(nome, "erroNome");
        
        var remInicialInvalido = this.validarCampoVazio(remuneracaoInicial, "erroRemuneracaoInicial");
        
        var remFinalInvalido = this.validarCampoVazio(remuneracaoFinal, "erroRemuneracaoFinal");

        // Validação da data.
        var dataInvalida = DataInvalida(dataObjeto, data);
        this.setState({
            erroDataInvalida: dataInvalida
        }, () => {
            if(!nomeInvalido && !remInicialInvalido && !remFinalInvalido && !dataInvalida)
                this.props.setPassoAtivo(2);
            else
                window.scrollTo(800, 350);
        });
    }

    // Método que atualiza o state da data e da idade ao tirar o foco do campo Data de Nascimento.
    onBlurData() {
        var data = document.getElementById("dataNascimento").value;
        var dataObjeto = this.converteData(data);

        var dataInvalida = DataInvalida(dataObjeto, data);
        
        this.setState({
            erroDataInvalida: dataInvalida,
            idade: calculaIdade(dataObjeto)
        });
    }

    // Método responsável por converter a data recebida (em string, no formato 'dd/mm/aaaa') para date (Objeto).
    converteData(dataString) {
        var dataPartes = dataString.split("/");
        return new Date(dataPartes[2], dataPartes[1] - 1, dataPartes[0]);
    }	

    validaExtremosComboAposentadoria() {
        if(this.state.idade < 40)
            return 40;
        else if(this.state.idade >= 40 & this.state.idade <= 75)
            return this.state.idade;
        else
            return 75;
    }

    componentDidMount() {
        window.scrollTo(0, 0);
    }

    render() {
        return (
            <div>
                <Campo id="nome" label="Qual o seu nome?" mensagemErro="Campo Obrigatório" mostrarErro={this.state.erroNome}>
                    <div className="col">
                        <input data-parsley-minlenght="3" type="text" className="form-control name" placeholder="Insira seu nome" id="nome" />
                    </div>
                </Campo>

                <Campo id="dataNascimento" label="Qual a sua data de nascimento?" mensagemErro="Data inválida" mostrarErro={this.state.erroData || this.state.erroDataInvalida}>
                    <div className="col-lg-2 col-5">
                        <input type="text" className="form-control date" id="dataNascimento" placeholder="__/__/____" onBlur={this.onBlurData.bind(this)} />
                    </div>
                </Campo>
                <Campo id="tipoAtivoNormal" label="Você é um" usaBotaoAjuda textoModal={textos.ativoNormal} textoModal2={textos.ativoFacultativo} >
                    <div className="col-lg-2 col-5">
                        <div className="form-check">
                            <input name="active" className="form-check-input" type="radio" value="option1" id="tipoAtivoNormal" defaultChecked></input>
                            <label className="form-check-label" htmlFor="tipoAtivoNormal">Ativo Normal</label>
                        </div>
                        <div className="form-check">
                            <input name="active" className="form-check-input" type="radio" value="option2" id="tipoAtivoFacultativo"></input>
                            <label className="form-check-label" htmlFor="tipoAtivoFacultativo">Ativo Facultativo</label>
                        </div>
                    </div>
                </Campo>

                <Campo id="idadeAposentadoria" label="Você pretende se aposentar com quantos anos?" usaBotaoAjuda textoModal={textos.idadeAposentadoria}>
                    <div className="col-lg-1 col-4">
                        <Combo id="idadeAposentadoria" min={this.validaExtremosComboAposentadoria()} max={75} incremento={1}  />
                    </div>
                </Campo>

                <Campo id="tempoAposentadoria" label="Por quantos anos você pretende receber sua aposentadoria?" usaBotaoAjuda textoModal={textos.tempoAposentadoria}>
                    <div className="col-lg-1 col-4">
                        <Combo id="tempoAposentadoria" min={20} max={50} selected={20} />
                    </div>
                </Campo>

                <Campo id="tempoSobrevivencia" label="Por quantos anos você pretende receber sobrevivência?" usaBotaoAjuda textoModal={textos.tempoSobrevivencia}>
                    <div className="col-lg-1 col-4">
                        <Combo id="tempoSobrevivencia" min={5} max={20} selected={5} />
                    </div>
                </Campo>

                <Campo id="remuneracaoInicial" label="Remuneração inicial" label2="(conforme tabela de progressão)" usaBotaoAjuda textoModal={textos.remuneracaoInicial} mensagemErro="Campo Obrigatório" mostrarErro={this.state.erroRemuneracaoInicial}>
                    <div className="col-lg-3 col-10">
                        <div className="input-group">
                            <div className="input-group-prepend">
                                <span className="input-group-text">R$</span>
                            </div>

                            <input type="text" className="form-control money" aria-label="Amount" placeholder="Insira a remuneração inicial" id="remuneracaoInicial" required />
                        </div>
                    </div>
                </Campo>

                <Campo id="remuneracaoFinal" label="Remuneração final" label2="(conforme tabela de progressão)" usaBotaoAjuda textoModal={textos.remuneracaoFinal} mensagemErro="Campo Obrigatório" mostrarErro={this.state.erroRemuneracaoFinal}>
                    <div className="col-lg-3 col-10">
                        <div className="input-group">
                            <div className="input-group-prepend">
                                <span className="input-group-text">R$</span>
                            </div>
                            
                            <input type="text" className="form-control money" aria-label="Amount" placeholder="Insira a remuneração final" id="remuneracaoFinal" required />
                        </div>
                    </div>
                </Campo>

                <Campo id="taxaCrescimentoSalarial" label="Taxa de crescimento salarial a cada dois anos" usaBotaoAjuda textoModal={textos.taxaCrescimentoSalarial}>
                    <div className="col-lg-2 col-10">
                        <div className="input-group">
                            <Combo id="taxaCrescimentoSalarial" min={2} max={3.00} selected={2} decimais incremento={0.10} />
                            <div className="input-group-append">
                                <span className="input-group-text">%</span>
                            </div>
                        </div>
                    </div>
                </Campo>

                <Campo id="taxaJurosReal" label="Taxa de Juros Real" label2="(meta financeira)" usaBotaoAjuda textoModal={textos.taxaJurosReal}>
                    <div className="col-lg-1 col-2">
                        <label className="form-control-plaintext form-control-lg font-weight-bold text-primary">4,5%</label>
                    </div>
                </Campo>
                <br/>
                <div className="alert alert-danger">
                    <h4>Antes de continuar, é muito importante entender as informações abaixo:</h4>
                    <p align="justify">
                        A) As informações constantes nesta simulação partem de dados informados pelos participantes no ato da opção pela previdência complementar. B) A simulação
                        apresentará resultado diferenciado, caso o participante altere, durante sua vida funcional, os dados aqui fornecidos. C) Toda simulação trabalha com eventos
                        futuros incertos e seus resultados são completamente estimados em relação a estes eventos. D) O valor da remuneração paga pelo rpps somente será efetivamente
                        conhecido quando o servidor tornar-se elegível ao benefício requerido. E) A preves não se responsabiliza pelas informações prestadas pelo participante para a 
                        geração da simulação previdenciária. F) Os resultados apresentados, referentes a esta simulação, não são garantia de benefício futuro. G) Benefício Pecúlio por 
                        Morte (598) N° Processo SUSEP: 15414.005367/2012-77 H) Benefício Pecúlio por Invalidez (599) N° Processo SUSEP: 15414.005364/2012-33 I) O registro destes planos 
                        na SUSEP não implica, por parte da Autarquia, incentivo ou recomendação para sua comercialização. J) BENEFÍCIOS DE RISCO - PECÚLIOS GARANTIDOS PELA MONGERAL AEGON 
                        SEGUROS E PREVIDÊNCIA S.A - CNPJ: 33.608.308/0001-73
                    </p>
                    <hr/>

                    <h4 className="col">
                        <input className="form-check-input" type="checkbox" id="agree" onChange={this.toggleTermos.bind(this)} />
                        <label className="form-check-label" htmlFor="agree">Li e entendi!</label>
                    </h4>
                </div>

                <div className="text-center">
                    <button className="btn btn-primary btn-lg" id="submit-button" onClick={() => this.validaPasso()} disabled={!this.state.termosAceitos}>Continuar</button>
                </div>
            </div>
        );
    }
}



