import React from "react";
import { Passo1, Passo2, Passo3 } from ".";

export default class MasterPage extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            passo: 1,

            termosAceitos: false,
            idade: 40,

            // States de validação do Passo 1
            erroNome: false,
            erroDataInvalida: false,
            erroRemuneracaoInicial: false,
            erroRemuneracaoFinal: false,

            percentualBasico: "3.50",
            beneficioRiscoInvalidez: "0.00",
            beneficioRiscoInvalidezMax: "1.00",
            beneficioRiscoPensaoMorte: "0.00",
            beneficioRiscoPensaoMorteMax: "1.00",
            beneficioRiscoSoma: "0",
            sobrevivencia: "0.00",
            carregamentoContribuicaoBasica: "0.21",
            aposentadoriaAdicional: "0",
            invalidezAdicional: "0",
            pensaoMorteAdicional: "0",
            sobrevivenciaAdicional: "0",
            carregamentoContribuicaoAdicional: "0.00"
        };
    }

    setPassoAtivo(passo) {
        this.setState({ passo: passo });
    }

    toggleModal() {
        this.setState({ modalVisivel: !this.state.modalVisivel })
    }

    render() {
        return (
            <div className="container">
                <div align="center">
                    <img className="logo" src="img/logo.png" alt="" />
                    <h2>Bem Vindo ao</h2>
                    <h2>Simulador de Benefício PREVES SE</h2><br />

                    <AbasPassos passo={this.state.passo} setPassoAtivo={this.setPassoAtivo.bind(this)} />
                </div>

                <ConteudoPassos ativo={this.state.passo} setPassoAtivo={this.setPassoAtivo.bind(this)} state={this.state} />
            </div>
        );
    }
}

const ConteudoPassos = (props) => {
    if(props.ativo === 1)
        return <Passo1 setPassoAtivo={props.setPassoAtivo.bind(this)} state={props.state} />
    else if(props.ativo === 2)
        return <Passo2 setPassoAtivo={props.setPassoAtivo.bind(this)} state={props.state} />
    else
        return <Passo3 setPassoAtivo={props.setPassoAtivo.bind(this)} state={props.state} />
};

const AbasPassos = (props) => (
    <div id="passos">
        <PassoBotao id={1} ativo={props.passo} titulo="Passo 1" subtitulo="Dados Básicos" setPassoAtivo={props.setPassoAtivo.bind(this)} />
        <PassoBotao id={2} ativo={props.passo} titulo="Passo 2" subtitulo="Contribuição" setPassoAtivo={props.setPassoAtivo.bind(this)} />
        <PassoBotao id={3} ativo={props.passo} titulo="Passo 3" subtitulo="Resultado" setPassoAtivo={props.setPassoAtivo.bind(this)} />
    </div>
);

const PassoBotao = (props) => (
    <button className={"btn " + estadoPasso(props.id, props.ativo)} disabled={props.ativo < props.id}
            onClick={() => props.setPassoAtivo(props.id)}>
        <span>{props.titulo}</span>
        <h3>{props.subtitulo}</h3>
    </button>
);

function estadoPasso(id, passoAtivo) {
    if(passoAtivo === id)
        return "btn-secondary";
    else if(passoAtivo > id)
        return "btn-primary";
    else
        return "btn-light";
}



